
#app {
  font-family: "Raleway", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #111;
  background-color: white;
}
.button {
  margin: 0px 15px;
  border: 1px solid black;
  border-radius: 5px;
}
.button:hover {
  background-color: #CCC;
}
span {
  display: block;
  margin-bottom: 10px;
}
a {
  text-decoration: none;
  color: #555;
  font-weight: bold;
  font-style: italic;
}
header {
  position: relative;
  top: 0px;
  margin-left: 10px;
  width: calc(100% - 20px);
  z-index: 900;
  background-color: #333;
  box-shadow: 0px 2px 10px #888;
  color: white;
  border-radius: 0px 0px 10px 10px;
  height: 4.5em;
}
header span {
  padding-top: 0.5em;
  font-size: 2em;
  font-weight: bold;
}
header img {
  padding-top: 0.5em;
  padding-left: 0.5em;
  float: left;
  height: 80%;
  cursor: pointer;
}
#time {
  padding-top: 10px;
}
.scroll {
  overflow: auto;
}
.fullpage {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  z-index: 950;
}
.menu {
  z-index: 990;
  box-shadow: 0px 2px 10px #444;
  background-color: #FFF;
}
.menu:after {
  content:'';
  position: absolute;
  top: 0px;
  left: 13px;
  margin-top: -10px;
  width: 0;
  height: 0;
  border-bottom: solid 10px white;
  border-left: solid 10px transparent;
  border-right: solid 10px transparent;
}
.menu span {
  text-align: left;
  padding: 20px;
  margin: 0px;
  font-size: 1.2em;
  cursor: pointer;
}
.menu span img {
  width: 1em;
  height: 1em;
  float: left;
  margin-top: 2px;
  padding-right: 10px;
}
.score {
  width: 90%;
  margin: auto;
  margin-top: 8px;
  border: 1px solid black;
  border-radius: 8px;
}
.score table {
  width: 90%;
  margin: 5%;
  padding: 2px;
  border-collapse: collapse;
}
.score i {
  border-bottom: 1px solid black;
}
.bold {
  font-weight: bold;
}
.score tr:nth-child(2n) {
  background-color: #EEE;
}
.score tr:nth-child(2n+1) {
  background-color: #CCC;
}
@media (min-aspect-ratio: 4/3) {
  /* LANDSCAPE */
.menu {
    position: fixed;
    top: 4.8em;
    left: calc(23px + 100vh);
}
#field {
    position: fixed;
    background-color: #FAFAFA;
    overflow: hidden;
    top: 0px;
    left: 0px;
    min-width: 50vw;
    width: 100vh;
    max-width: calc(100vw - 300px);
    height: 100vh;
    box-shadow: 0px 0px 2px #444 inset;
}
#flow {
    position: fixed;
    right: 0px;
    top: 0px;
    min-width: 300px;
    width: calc(100vw - 100vh);
    max-width: 50vw;
    height: 100vh;
    box-shadow: 0px 0px 3px #444;
    background-color: white;
    z-index: 5;
    overflow: auto;
}
}
@media (min-aspect-ratio: 3/4) and (max-aspect-ratio: 4/3)  {
  /* QUADRATIC (approx) */
#flow {
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100vw;
    height: 100vh;
}
.menu {
    position: absolute;
    top: 4.8em;
    left: 0px;
}
#field {
    position: relative;
    float: left;
    background-color: #FAFAFA;
    overflow: hidden;
    width: 50vw;
    height: calc(100vh - 5.5em - 20px);
}
#buttons {
    position: absolute;
    left: 50vw;
    background-color: #FAFAFA;
    width: 50vw;
    top: calc(50vh + 2.25em + 10px);
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}
}
@media (max-aspect-ratio: 3/4) {
  /* PORTRAIT */
.menu {
    position: absolute;
    top: 5em;
    left: 20px;
}
#field {
    position: relative;
    background-color: #FAFAFA;
    overflow: hidden;
    box-shadow: 0px 0px 2px #444 inset;
    /*top: 7em;
    left: 0px;*/
    width: 100vw;
    height: 100vw;
    max-height: calc(100vh - 7em - 200px);
    z-index: 200;
}
#flow {
    position: fixed;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    box-shadow: 0px 0px 3px #444;
    background-color: white;
    z-index: 5;
    overflow: hidden;
}
#buttons {

    height: calc(100vh - 100vw - 7em);
    min-height: 200px;
    overflow: auto;
}
  /*#buttons {
    position: absolute;
    left: 0px;
    top: calc(100vw + 7.5em);
    width: 100vw;
    height: 30%;
  }
  #time {
    position: absolute;
    left: 0px;
    top: 5.4em;
    width: 100vw;
  }*/
}
@media print {
#buttons {
    visibility: collapse;
}
#time {
    visibility: collapse;
}
}




